.main {
  height: 90vh;
  padding: 10px;
  margin: 30px;
}
.card {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}

.Deck {
  margin-top: 80px;
}

.title {
  color: azure;
  font-size: 40px;
  text-transform: uppercase;
  letter-spacing: 15px;
}

.title2 {
  font-size: 20px;
  color: #4faaa173;
  letter-spacing: 0.1rem;
}

.btn {
  background: none;
  outline: none;
  font: inherit;
  margin: 0.5em;
  padding: 1em 2em;
  border: 2px solid;
  color: #009688;
  transition: 0.25s;
}
.btn:hover {
  border-color: #4caf50;
  color: wheat;
  box-shadow: inset 0 0 0 2em #009688;
}

@media only screen and (max-width: 600px) {
  * {
    font-size: 15px;
    margin: 0;
  }
  .card {
    width: 55%;
  }
  .title {
    margin-top: 15px;
    font-size: 15px;
  }
  .title2 {
    margin-top: 10px;
    font-size: 15px;
  }
  .btn {
    margin-top: 15px;
  }
}
